define('em/routes/cards/index', ['exports', 'ember', 'em/models/card'], function (exports, Ember, Card) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    queryParams: {
      page: {
        refreshModel: true
      },
      query: {
        refreshModel: true
      }
    },

    model: function model(params) {
      return Card['default'].all(params);
    }
  });

});